import React from "react";
import { Link } from "react-router-dom";
import "./Recipes.css";

const Recipes = () => {
  const recipes = [
    {
      id: 1,
      title: "Bapnarlar Pastrmal Humus",
      description:
        "Pastrmal humus iin ncelikle halanm nohutlatn kabuklarn soyalm. Bu ekilde humusumuz daha przsz olacaktr. Ardndan blendere alalm. zerine tahin, sarmsak, kimyon ve tuzu ekleyelim.",
      image: "/images/yemek_tarif/pastrmalhumus.png",
    },
    {
      id: 2,
      title: "Pastrmal Frn Patates",
      description:
        "Patatesleri iyice ykayp temizleyelim. Soymadan zerine bakla derin kesikler atarak 180 derece frnda iyice yumuayana ve kabuklar da kzarana kadar piirelim.",
      image: "/images/yemek_tarif/patates.png",
    },
    {
      id: 3,
      title: "Spagetti Bolognese",
      description:
        "Spagetti iin: 1 paket spagetti makarna, 1 litre su, 1 tatl ka tuz, 1 yemek ka zeytinya. Bolonez Sos iin: 250 gram kyma, 1 adet orta boy soan, 1 adet orta boy havu, 1 adet orta boy domates, 2 di sarmsak, 3 yemek ka zeytinya.",
      image: "/images/yemek_tarif/spagetti.png",
    },
  ];

  return (
    <div className="recipes page-container">
      <div className="recipes-hero" style={{ background: '#111', color: '#fff', padding: '4rem 0', textAlign: 'center' }}>
        <h1 style={{ color: '#fff', fontSize: '2.5rem', marginBottom: '1rem', textTransform: 'uppercase', letterSpacing: 2, fontWeight: 800, textShadow: '0 2px 8px #000' }}>YEMEK TARFLER</h1>
        <p style={{ color: '#fff', fontSize: '1.2rem', maxWidth: 800, margin: '0 auto' }}>
          Bapnarlar et rnleri ile hazrlayabileceiniz lezzetli tarifler
        </p>
      </div>

      <div className="container">
        <div className="recipes-grid">
          {recipes.map((recipe) => (
            <div className="recipe-card" key={recipe.id}>
              <div className="recipe-image">
                <img src={recipe.image} alt={recipe.title} />
              </div>
              <div className="recipe-content">
                <h3>{recipe.title}</h3>
                <p className="recipe-description">{recipe.description}</p>
                <Link to={`/recipes/${recipe.id}`} className="recipe-button">
                  DETAYLI OKU
                </Link>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Recipes;
