import React, { useRef, useState } from "react";
import "../styles/MeatCuts.css";

const cuts = [
  {
    title: "Boyun Blm",
    cuts: [
      {
        name: "Gerdan",
        description:
          "Danann boyun blmnde elde edilir. Kyma, orba ve yahni yapmnda kullanlr.",
        uses: ["Kyma", "orba", "Yahni"],
      },
    ],
  },
  {
    title: "Gs Blm",
    cuts: [
      {
        name: "Gs",
        description:
          "Dana etinin gs ksmndan elde edilir. Halama, orba ve kyma yapmnda kullanlr.",
        uses: ["Halama", "orba", "Kyma"],
      },
    ],
  },
  {
    title: "Srt Blm",
    cuts: [
      {
        name: "Antrikot",
        description:
          "Srtn st ksmndan elde edilir. Izgara ve biftek yapm iin idealdir.",
        uses: ["Izgara", "Biftek"],
      },
      {
        name: "Bonfile",
        description:
          "Srtn en i ksmndan elde edilir. En yumuak ve deerli et parasdr.",
        uses: ["Izgara", "Biftek", "Rosto"],
      },
      {
        name: "Kontrfile",
        description:
          "Srtn alt ksmndan elde edilir. Izgara ve biftek iin uygundur.",
        uses: ["Izgara", "Biftek"],
      },
      {
        name: "Sokum",
        description:
          "Srtn arka ksmnda, bonfileye yakn bir blgedir. Yumuak ve lezzetli bir ettir.",
        uses: ["Izgara", "Biftek", "Kavurma"],
      },
      {
        name: "Tran",
        description:
          "Srt ve but arasnda yer alr. Lifli yaps ile rosto ve halama iin uygundur.",
        uses: ["Rosto", "Halama", "Biftek"],
      },
      {
        name: "Kontrnuar",
        description:
          "Trann hemen yannda, butun st ksmnda yer alr. Rosto ve halama iin kullanlr.",
        uses: ["Rosto", "Halama"],
      },
    ],
  },
  {
    title: "But Blm",
    cuts: [
      {
        name: "Nuar",
        description:
          "Butun st ksmndan elde edilir. Rosto ve biftek yapmnda kullanlr.",
        uses: ["Rosto", "Biftek"],
      },
      {
        name: "Bodigo",
        description:
          "Butun arka ksmnda yer alr. Halama ve tencere yemekleri iin uygundur.",
        uses: ["Halama", "Tencere Yemei"],
      },
      {
        name: "Yumurta",
        description:
          "Butun i ksmnda, kk yuvarlak bir ettir. Halama ve sote iin uygundur.",
        uses: ["Halama", "Sote"],
      },
      {
        name: "Peneta",
        description:
          "Butun alt ksmnda, yal ve lezzetli bir ettir. Kyma ve kfte yapmnda kullanlr.",
        uses: ["Kyma", "Kfte"],
      },
      {
        name: "Pirzola",
        description:
          "Butun orta ksmndan elde edilir. Izgara iin idealdir.",
        uses: ["Izgara", "Tavada"],
      },
    ],
  },
  {
    title: "Gs ve Karn Blm",
    cuts: [
      {
        name: "D",
        description:
          "Danann alt karn ksmnda yer alr. Kyma, halama ve tencere yemekleri iin uygundur.",
        uses: ["Kyma", "Halama", "Tencere Yemei"],
      },
    ],
  },
  {
    title: "n Kol ve Krek Blm",
    cuts: [
      {
        name: "Krek",
        description:
          "Danann n bacanda, kol ksmnda yer alr. Halama, sote ve kyma iin uygundur.",
        uses: ["Halama", "Sote", "Kyma"],
      },
    ],
  },
];

const MeatCuts = () => {
  const [showMagnifier, setShowMagnifier] = useState(false);
  const [magnifierPosition, setMagnifierPosition] = useState({ x: 0, y: 0 });
  const [imgSize, setImgSize] = useState({ width: 0, height: 0 });
  const imgRef = useRef(null);

  const magnifierSize = 80;
  const zoom = 2.2;

  const handleMouseMove = (e) => {
    const rect = imgRef.current.getBoundingClientRect();
    setMagnifierPosition({
      x: e.clientX - rect.left,
      y: e.clientY - rect.top,
    });
  };

  const handleMouseEnter = () => {
    setShowMagnifier(true);
    if (imgRef.current) {
      setImgSize({ width: imgRef.current.width, height: imgRef.current.height });
    }
  };

  const handleMouseLeave = () => {
    setShowMagnifier(false);
  };

  return (
    <div className="page-container" style={{ maxWidth: 1400, margin: "0 auto", padding: "2rem", background: "#f8f9fa" }}>
      <div className="header-image" style={{ position: 'relative', width: '100%', maxWidth: 600, margin: '0 auto 2rem' }}>
        <img
          ref={imgRef}
          src="/images/dana_parts.png"
          alt="Dana Eti Blmleri"
          style={{ width: '100%', height: 400, objectFit: 'contain', borderRadius: 8, boxShadow: '0 4px 6px rgba(0,0,0,0.1)' }}
          onMouseMove={handleMouseMove}
          onMouseEnter={handleMouseEnter}
          onMouseLeave={handleMouseLeave}
        />
        {showMagnifier && (
          <div
            style={{
              position: 'absolute',
              pointerEvents: 'none',
              top: magnifierPosition.y - magnifierSize / 2,
              left: magnifierPosition.x - magnifierSize / 2,
              width: magnifierSize,
              height: magnifierSize,
              borderRadius: '50%',
              boxShadow: '0 4px 16px #80002044',
              border: '2.5px solid #d4af37',
              background: `url(/images/dana_parts.png)`,
              backgroundRepeat: 'no-repeat',
              backgroundSize: `${imgSize.width * zoom}px ${imgSize.height * zoom}px`,
              backgroundPosition: `-${magnifierPosition.x * zoom - magnifierSize / 2}px -${magnifierPosition.y * zoom - magnifierSize / 2}px`,
              zIndex: 10,
            }}
          />
        )}
      </div>

      <h1 className="page-title">Dana Eti Blmleri ve Yemeklerde Kullanm</h1>

      <div className="content-section">
        <p className="intro-text">
          Dana etini herkes sever ancak biftek, bonfile, antrikot danann hangi blmnde yer alr diye sorulsa birok kii cevap veremez. imdi dana eti blmleri ve bu blmlerin hangi yemekler iin nasl kullanldndan bahsedelim.
        </p>

        {/* 2x2 grid for sections */}
        <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '2.5rem', marginBottom: 0 }}>
          {/* First row: Boyun & Gs */}
          {[0, 1].map((i) => (
            <div key={i} className="cut-section">
              <h2 className="section-title">{cuts[i].title}</h2>
              <div className="cuts-list">
                {cuts[i].cuts.map((cut, cutIndex) => (
                  <div key={cutIndex} className="cut-card">
                    <h3 className="cut-name">{cut.name}</h3>
                    <p className="cut-description">{cut.description}</p>
                    <div className="cut-uses">
                      <h4>Kullanm Alanlar:</h4>
                      <ul>
                        {cut.uses.map((use, useIndex) => (
                          <li key={useIndex}>{use}</li>
                        ))}
                      </ul>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          ))}
        </div>
        <div style={{ height: 32 }} />
        <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '2.5rem' }}>
          {/* Second row: Srt & But */}
          {[2, 3].map((i) => (
            <div key={i} className="cut-section">
              <h2 className="section-title">{cuts[i].title}</h2>
              <div className="cuts-list">
                {cuts[i].cuts.map((cut, cutIndex) => (
                  <div key={cutIndex} className="cut-card">
                    <h3 className="cut-name">{cut.name}</h3>
                    <p className="cut-description">{cut.description}</p>
                    <div className="cut-uses">
                      <h4>Kullanm Alanlar:</h4>
                      <ul>
                        {cut.uses.map((use, useIndex) => (
                          <li key={useIndex}>{use}</li>
                        ))}
                      </ul>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default MeatCuts;
