import React from "react";
import "../styles/MeatQuality.css";

const MeatQuality = () => (
  <div className="meat-quality-container">
    <h2 className="section-title">ET Nasl Olmal?</h2>
    <div
      className="et-nasil-olmali-section"
      style={{
        marginBottom: '2rem',
        background: `linear-gradient(rgba(255,255,255,0.85), rgba(255,255,255,0.85)), url('/images/et_resimler/etnasil.png') center/cover no-repeat`,
        padding: '1.5rem',
        borderRadius: 8,
        minHeight: 220,
        boxShadow: '0 2px 12px #80002011'
      }}
    >
      <p style={{ fontWeight: 500, color: '#333', fontSize: '1.1rem' }}>
        Etin iyi olmas iin ncelikli olarak yetitii doann et yetitiriciliine uygun olmas gerekir, evresel koullar ve hayvann besin kaynaklar etin besleyiciliini, lezzetini belirleyen en nemli faktrlerdendir.<br/><br/>
        Bir dier faktr ise hayvann rkdr, eer rk yetitii blge ile uyum gstermese, tm koullar uygun olsa bile uygun et verimi alnamaz. Ayrca iyi et elde etmek iin, hayvann beslenirken, sadece doal yemler yemesi, uygun olmayan ila almamas, asla hormon kullanlmamas gerekir. Bu zengin besin kayna yetitirilen hayvanlarn etinin mineral bakmndan doru ve yeterli seviyede olmasn salamaktadr.
      </p>
    </div>
    <h2 className="section-title">Et Satn Alrken Dikkat Edilmesi Gerekenler?</h2>
    <div
      className="et-satin-alirken-section"
      style={{
        marginBottom: '2rem',
        background: `linear-gradient(rgba(255,255,255,0.85), rgba(255,255,255,0.85)), url('/images/et_resimler/satinalirken.png') center/cover no-repeat`,
        padding: '1.5rem',
        borderRadius: 8,
        minHeight: 220,
        boxShadow: '0 2px 12px #80002011'
      }}
    >
      <p style={{ fontWeight: 500, color: '#333', fontSize: '1.1rem' }}>
        Et alrken ilk baklmas gereken ey, salk koullardr. Salkl etin anlalmas iin ilk baklmas gereken etin damgasdr.<br/>
        Tm etlerin zerinde, salk kontrollerinin yapldna ve uygun bir mezbahada kesildiine dair damga vardr. Genellikle mavi renkli olan bu damga vitrinde grdnz etlerde mutlaka olmaldr.<br/><br/>
        kinci olarak kontrol edilmesi gereken ey ise salk raporlardr, bu raporlar mezbahada resmi veterinerler tarafndan dzenlenir ve sat noktalarnda bir nshasnn bulunmas gerekir. Et alrken bu raporlar sorulmal ve tarih bakmndanda kontrol edilmelidir.<br/><br/>
        Bunlarn dnda et alnacak noktann temiz olmas, eti hazrlayacak personelin eldiven kullanmasna ve kiisel hijyeni kontrol edilmelidir. Satn alnacak etler, canl pembe renkte olmal, etrafnda kararma olmamalr. Etler reyonda baka rnlere temas etmemeli, soukta uygun artlarda muhafaza ediliyor olmaldrlar.<br/><br/>
        Et seildikten sonra hazrlanrken mutlaka uygun yzeyde, tavuk vb. gibi dier rnler ile temas etmeden hazrlanmaldr. Hazrlandktan sonra gdaya uygun ambalaj ile paketlenmeli ve mmkn olan en ksa srede eve gtrlerek buzdolabna konmaldr.
      </p>
    </div>
    <h1 className="page-title">Aldmz Etin Kaliteli Olduunu Nasl Anlarz?</h1>
    <div className="intro-section">
      <p>Bonfile, kontrfile, T-bone steak, pirzola Favoriniz hangisi olursa olsun, satn alrken ve piirirken dikkat etmeniz gereken nemli noktalar var. Mkemmel lezzette bir krmz et hazrlamak iin ncelikle onu biraz daha yakndan tanmaya ne dersiniz?</p>
    </div>
    <div className="quality-section">
      <h2>Etin Kalitesini Anlamann Yollar</h2>
      <ul className="quality-list">
        <li><b>Renk:</b> Taze dana eti parlak krmz, kuzu eti ise ak pembe renkte olur.</li>
        <li><b>Koku:</b> Kaliteli etin kendine has ho bir kokusu vardr, ar ve eki kokular olmamal.</li>
        <li><b>Doku:</b> Etin dokusu sk ve elastik olmal, parmakla bastrnca eski haline dnmeli.</li>
        <li><b>Ya Dalm:</b> Mermerimsi ya dalm (marbling) lezzet ve yumuaklk iin nemlidir.</li>
        <li><b>Nem:</b> Et yzeyi hafif nemli olmal, ar slak veya kuru olmamal.</li>
      </ul>
    </div>
    <div className="time-section">
      <h2>Piirme Zaman ve Scakl</h2>
      <div className="time-grid">
        <div className="time-item">
          <h3>Az Pimi (Rare)</h3>
          <p> scaklk: 50-52C<br/>Ksa srede yksek atete piirilir, ii krmz ve sulu kalr.</p>
        </div>
        <div className="time-item">
          <h3>Orta Pimi (Medium)</h3>
          <p> scaklk: 60-65C<br/>Daha uzun sre piirilir, ii pembe ve sulu olur.</p>
        </div>
        <div className="time-item">
          <h3>yi Pimi (Well Done)</h3>
          <p> scaklk: 70C ve zeri<br/>Et tamamen pier, ii kahverengi ve daha az sulu olur.</p>
        </div>
      </div>
    </div>
    <details style={{ marginTop: '2rem' }}>
      <summary style={{ cursor: 'pointer', fontWeight: 600, color: 'var(--primary-color)', fontSize: '1.1rem' }}>tamamn oku</summary>
      <div style={{ marginTop: '1rem', background: '#f8f9fa', padding: '1rem', borderRadius: 8 }}>
        <p>
          Etin iyi olmas iin ncelikli olarak yetitii doann et yetitiriciliine uygun olmas gerekir, evresel koullar ve hayvann besin kaynaklar etin besleyiciliini, lezzetini belirleyen en nemli faktrlerdendir.<br/><br/>
          Bir dier faktr ise hayvann rkdr, eer rk yetitii blge ile uyum gstermese, tm koullar uygun olsa bile uygun et verimi alnamaz. Ayrca iyi et elde etmek iin, hayvann beslenirken, sadece doal yemler yemesi, uygun olmayan ila almamas, asla hormon kullanlmamas gerekir. Bu zengin besin kayna yetitirilen hayvanlarn etinin mineral bakmndan doru ve yeterli seviyede olmasn salamaktadr.
        </p>
      </div>
    </details>
  </div>
);

export default MeatQuality;
