import React from 'react';

const About = () => {
  const features = [
    {
      id: 1,
      title: 'Kaliteli Et',
      description: 'En kaliteli et rnlerini mterilerimize sunuyoruz.',
      icon: 'fas fa-star'
    },
    {
      id: 2,
      title: 'Taze rnler',
      description: 'Tm rnlerimiz gnlk olarak taze ekilde hazrlanr.',
      icon: 'fas fa-leaf'
    },
    {
      id: 3,
      title: 'Uzman Ekip',
      description: 'Alannda uzman ekibimiz ile hizmet veriyoruz.',
      icon: 'fas fa-users'
    },
    {
      id: 4,
      title: 'Hijyenik Ortam',
      description: 'Tm ilemlerimiz hijyenik ortamlarda gerekletirilir.',
      icon: 'fas fa-shield-alt'
    }
  ];

  return (
    <div>
      {/* Hero Section */}
      <section className="about-hero">
        <div className="container">
          <h1>Hakkmzda</h1>
          <p>Kalite ve lezzetin adresi</p>
        </div>
      </section>

      {/* About Content */}
      <section className="about-content">
        <div className="container">
          <div className="about-text">
            <h2>Biz Kimiz?</h2>
            <p>
              Yllarn deneyimi ve uzman kadromuz ile en kaliteli et rnlerini mterilerimize sunuyoruz.
              Taze ve lezzetli rnlerimiz ile sizlere hizmet vermekten gurur duyuyoruz.
            </p>
            <p>
              Mteri memnuniyetini n planda tutarak, hijyenik ortamlarda hazrlanan rnlerimizi
              en uygun fiyatlarla sizlere ulatryoruz.
            </p>
          </div>

          {/* Features Grid */}
          <div className="features-grid">
            {features.map(feature => (
              <div key={feature.id} className="feature-card">
                <i className={feature.icon}></i>
                <h3>{feature.title}</h3>
                <p>{feature.description}</p>
              </div>
            ))}
          </div>
        </div>
      </section>
    </div>
  );
};

export default About; 