import React, { useEffect, useState } from "react";
import { useParams, Link } from "react-router-dom";
import "./RecipeDetail.css";

const RecipeDetail = () => {
  const { id } = useParams();
  const [recipe, setRecipe] = useState(null);

  useEffect(() => {
    // This would typically fetch from an API, but we're using static data
    const recipes = [
      {
        id: 1,
        title: "Bapnarlar Pastrmal Humus",
        shortDescription:
          "Geleneksel humus tarifimize Bapnarlar'n zel pastrmas ile lezzet katyoruz.",
        image: "/images/yemek_tarif/pastrmalhumus.png",
        ingredients: [
          "3 su barda halanm nohut",
          "1/3 su barda tahin",
          "1 adet limon suyu",
          "Yarm ay ka kimyon",
          "1 ay ka tuz",
          "2 di sarmsak",
        ],
        toppings: [
          "2 yemek ka zeytinya",
          "1 ay ka toz krmz biber",
          "4-5 dilim pastrma",
          "1 tatl ka tereya",
          "Maydanoz",
        ],
        instructions: [
          "Pastrmal humus iin ncelikle halanm nohutlatn kabuklarn soyalm. Bu ekilde humusumuz daha przsz olacaktr.",
          "Ardndan blendere alalm. zerine tahin, sarmsak, kimyon ve tuzu ekleyelim.",
          "Btn malzemeleri blenderde iyice pre haline gelene kadar ezelim. (Arzuya gre eer kvamn daha sv isterseniz birka kak su ilave edebilirsiniz).",
          "zeri iin ocaa aldmz tavaya zeytinyan alalm ve stalm. zerine toz krmz biberi ekleyerek yakmadan kzdralm ve ocaktan alalm.",
          "Ayr bir tavaya tereyan alalm ve eritelim.",
          "Eriyen yan zerine pastrmalar ekleyerek evirelim.",
          "Hazr olan humusumuzu geni bir servis tabana alarak yayalm.",
          "zerine hazrladmz toz biberli sosu, ince kylm maydanoz ve bir ka tane halanm nohutu yerletirelim.",
          "Son olarak pastrmalar da ekleyerek humusumuzu servis edelim. Afiyet olsun!",
        ],
      },
      {
        id: 2,
        title: "Pastrmal Frn Patates",
        shortDescription:
          "Lezzetli frn patateslerine Bapnarlar pastrmasnn muhteem elii ile doyumsuz bir lezzet.",
        image: "/images/yemek_tarif/patates.png",
        ingredients: [
          "4-5 adet orta boy patates",
          "8-10 dilim pastrma",
          "1 su barda rendelenmi kaar peyniri",
          "2 yemek ka zeytinya",
          "Tuz, karabiber",
          "(stee bal) 1 tatl ka krmz toz biber veya kekik",
        ],
        instructions: [
          "Patatesleri soyup ince dilimleyin.",
          "Zeytinya, tuz ve baharatlarla harmanlayn.",
          "Yal kat serili frn kabna dizin.",
          "180C frnda 25-30 dakika piirin.",
          "zerine pastrma dilimlerini ve kaar peynirini ekleyip 10 dakika daha frnlayn.",
          "Scak servis yapn.Afiyet olsun! ",
        ],
      },
      {
        id: 3,
        title:
          "Bapnarlar Kymasyla Hazrlanan Klasik talyan Bolonez Soslu Spagetti",
        shortDescription:
          "talyan mutfann en sevilen tariflerinden, Bapnarlar'n zel kymas ile hazrlanan enfes bolonez soslu spagetti.",
        image: "/images/yemek_tarif/spagetti.png",
        spagettiIngredients: [
          "1 paket (yaklak 500 g) spagetti makarna",
          "1 litre su",
          "1 tatl ka tuz",
          "1 yemek ka zeytinya",
        ],
        sauceIngredients: [
          "250 g Bapnarlar zel dana kyma",
          "1 adet orta boy kuru soan (ince doranm)",
          "1 adet orta boy havu (kk kpler halinde doranm)",
          "1 adet orta boy domates (rendelenmi)",
          "2 di sarmsak (ince kylm)",
          "3 yemek ka zeytinya",
          "1 ay ka tuz",
          "1 ay ka taze ekilmi karabiber",
        ],
        instructions: [
          "Geni bir tencerede suyu kaynamaya brakn. Su kaynadktan sonra tuzu ekleyin.",
          "Spagettiyi tencereye ilave edin ve ambalaj zerinde belirtilen srede, al dente kvamna gelene kadar halayn.",
          "Pien makarnay szn, soumamas iin 1 yemek ka zeytinya ile kartrn ve bekletin.",
          "Geni tabanl bir sote tavasnda zeytinyan stn.",
          "Soanlar ekleyin ve effaflaana kadar orta atete kavurun.",
          "Ardndan sarmsaklar ilave edip 1 dakika daha soteleyin.",
          "Kymay ekleyin ve rengi tamamen dnene kadar kavurmaya devam edin.",
          "Doranm havular ilave edin, birka dakika birlikte soteleyin.",
          "Son olarak rendelenmi domatesi, tuzu ve karabiberi ekleyin.",
          "Sosu kapa kapal ekilde, ksk atete 15-20 dakika piirin. Ara ara kartrarak kvamn kontrol edin.",
          "Spagettiyi servis tabana aldktan sonra bolonez sosla kaplayn.",
          "Son dokunu olarak birka yaprak taze fesleen veya defne yapra ile ssleyebilirsiniz. Afiyet olsun !",
        ],
      },
    ];

    const selectedRecipe = recipes.find((r) => r.id === parseInt(id));
    if (selectedRecipe) {
      setRecipe(selectedRecipe);
    }
  }, [id]);

  if (!recipe) {
    return (
      <div className="loading-container">
        <p>Tarif ykleniyor...</p>
      </div>
    );
  }

  return (
    <div className="recipe-detail page-container">
      <div className="recipe-detail-hero">
        <h1>{recipe.title}</h1>
      </div>

      <div className="container">
        <div className="recipe-detail-container">
          <div className="recipe-detail-content">
            <div className="recipe-detail-image">
              <img src={recipe.image} alt={recipe.title} />
            </div>

            <div className="recipe-detail-info">
              <p className="recipe-intro">{recipe.shortDescription}</p>

              <div className="recipe-ingredients">
                <h2>Malzemeler</h2>

                {recipe.ingredients && (
                  <ul className="ingredients-list">
                    {recipe.ingredients.map((ingredient, index) => (
                      <li key={index}>{ingredient}</li>
                    ))}
                  </ul>
                )}

                {recipe.toppings && (
                  <>
                    <h3>zeri iin:</h3>
                    <ul className="ingredients-list">
                      {recipe.toppings.map((topping, index) => (
                        <li key={index}>{topping}</li>
                      ))}
                    </ul>
                  </>
                )}

                {recipe.spagettiIngredients && (
                  <>
                    <h3>Spagetti iin:</h3>
                    <ul className="ingredients-list">
                      {recipe.spagettiIngredients.map((ingredient, index) => (
                        <li key={index}>{ingredient}</li>
                      ))}
                    </ul>
                  </>
                )}

                {recipe.sauceIngredients && (
                  <>
                    <h3>Bolonez Sos iin:</h3>
                    <ul className="ingredients-list">
                      {recipe.sauceIngredients.map((ingredient, index) => (
                        <li key={index}>{ingredient}</li>
                      ))}
                    </ul>
                  </>
                )}
              </div>
            </div>
          </div>

          <div className="recipe-instructions">
            <h2>Nasl Yaplr?</h2>
            <ol className="instructions-list">
              {recipe.instructions.map((step, index) => (
                <li key={index}>{step}</li>
              ))}
            </ol>
          </div>

          <div className="back-to-recipes">
            <Link to="/recipes" className="back-button">
              Tariflere Dn
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
};

export default RecipeDetail;
